@import "../fonts/FiraCode/fira_code.css";
@font-face {
  font-family: "yekan";
  src: url(../fonts/Yekan/Yekan.eot);
  src: url(../fonts/Yekan/Yekan.eot?#iefix) format("embedded-opentype"), url(../fonts/Yekan/Yekan.woff) format("woff"), url(../fonts/Yekan/Yekan.ttf) format("truetype"), url(../fonts/Yekan/YekanBold.ttf) format("truetype"), url(../fonts/Yekan/Yekan.svg#BYekan) format("svg");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'iransans';
  src: url(../fonts/iransans/IRANSansWeb.eot);
  src: url(../fonts/iransans/IRANSansWeb.eot?#iefix) format("eot"), url(../fonts/iransans/IRANSansWeb.woff) format("woff"), url(../fonts/iransans/IRANSansWeb.ttf) format("ttf");
  font-weight: 400;
}
@font-face {
  font-family: 'iransans';
  src: url(../fonts/iransans/IRANSansWeb_Light.eot);
  src: url(../fonts/iransans/IRANSansWeb_Light.eot?#iefix) format("eot"), url(../fonts/iransans/IRANSansWeb_Light.woff) format("woff"), url(../fonts/iransans/IRANSansWeb_Light.ttf) format("ttf");
  font-weight: 300;
}
@font-face {
  font-family: "bbcnassim";
  src: url(../fonts/BBCFont/BBCNassim.ttf) format("truetype"), url(../fonts/BBCFont/BBCNassim.otf) format("opentype");
  font-weight: normal;
  font-style: normal;
}
::selection {
  color: #F5F5DC;
  background: #303030;
}
body {
  background: #415B76;
  margin-top: 30px;
  font-family: "iransans", "bbcnassim", Tahoma, Verdana, Arial, sans-serif;
  font-size: 16px;
}
.bright {
  color: #C8FF00 !important;
}
.text-beige {
  color: #F5F5DC !important;
}
.text-purple {
  color: #BA83D4 !important;
}
.text-parsclick {
  color: #FF8000 !important;
}
.text-white {
  color: white;
}
.edit {
  font-family: "bbcnassim", "Helvetica Neue", Helvetica, Tahoma, Verdana, Arial, sans-serif;
  direction: ltr;
  text-align: left;
  float: left;
}
.large-text {
  font-size: 90px;
  font-weight: 900;
  margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "iransans", "bbcnassim", Tahoma, Verdana, Arial, sans-serif;
  color: #415B76;
  text-shadow: 0.5px 0.5px 10px #9B9B9B;
}
code {
  display: inline-block;
  direction: ltr;
  text-align: left;
  font-size: 80%;
  text-shadow: none;
  font-family: "Fira Code", menlo, "Source Code Pro", Consolas, monospace !important;
  text-rendering: optimizeLegibility;
  font-variant-ligatures: contextual;
  font-feature-settings: "calt" 1;
}
pre {
  direction: ltr;
  float: left;
  text-align: left;
  width: 100%;
  border: none;
  background-color: #272822;
  color: #F0C832;
  font-family: "Fira Code", menlo, "Source Code Pro", Consolas, monospace !important;
  text-rendering: optimizeLegibility;
  font-variant-ligatures: contextual;
  font-feature-settings: "calt" 1;
}
blockquote {
  border-right: 5px solid #eaeab4;
  border-left: 0;
  padding: 11px 50px;
}
p {
  font-family: "iransans", "bbcnassim", Tahoma, Verdana, Arial, sans-serif;
  line-height: 1.5em;
  color: #303030;
  padding: 0 2%;
  text-align: justify;
}
input,
select,
textarea {
  font-family: "bbcnassim", Tahoma, Verdana, sans-serif, serif;
}
ol {
  list-style-type: persian;
}
ul {
  margin-top: 5px;
}
aside h2,
aside h3 {
  font-size: 1.8em;
  color: #E74C3C;
  border-bottom: 1px dotted #415B76;
}
textarea {
  resize: vertical;
  padding: 15px !important;
}
option {
  direction: ltr;
}
legend {
  font-family: "bbcnassim", "Helvetica Neue", Helvetica, Tahoma, Verdana, Arial, sans-serif;
}
hr {
  margin: 22px 0;
  border: 1px solid #E5E5E5;
}
@media (max-width: 767px) {
  dd {
    float: left;
  }
}
@media (max-width: 767px) {
  .dl-horizontal dd {
    margin-left: 0;
  }
}
.lead {
  margin-top: 15px;
  margin-bottom: 5px;
}
.underline {
  border-bottom: 1px solid #415B76;
}
.list-group-item-text {
  margin-right: 20px;
}
.list-group-item li {
  margin-right: 5px;
}
a:hover,
a:focus {
  color: #E74C3C;
  text-decoration: none;
  transition: color 0.5s ease;
}
.container {
  background: #F5F5DC;
  -webkit-border-radius: 0 60px 0 0;
  -moz-border-radius: 0 60px 0 0;
  border-radius: 0 60px 0 0;
}
@media (max-width: 767px) {
  .container {
    -webkit-border-radius: 0 30px 0 0;
    -moz-border-radius: 0 30px 0 0;
    border-radius: 0 30px 0 0;
  }
}
.main {
  background: #F5F5DC;
  -webkit-animation: fadeIn 2s;
  -moz-animation: fadeIn 2s;
  -o-animation: fadeIn 2s;
  animation: fadeIn 2s;
}
.navbar {
  margin-bottom: 0;
  border-radius: 0;
}
.navbar-toggle {
  position: absolute;
  top: 2px;
  right: 2px;
  padding: 5px 10px;
  background-color: #415B76;
  border: 1px solid #DDD;
  border-radius: 4px;
}
.navbar-toggle:hover,
.navbar-toggle:focus {
  background-color: #F5F5DC;
}
.carousel {
  margin-bottom: 0;
}
.carousel-caption > * {
  color: #F5F5DC;
}
.carousel-caption {
  text-align: right;
  background-color: rgba(0, 0, 0, 0.3);
  padding-right: 2%;
  right: 0;
  left: 50%;
  bottom: 20%;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}
.carousel-fade .carousel-inner .item {
  transition-property: opacity;
}
.carousel-fade .carousel-inner .item,
.carousel-fade .carousel-inner .active.left,
.carousel-fade .carousel-inner .active.right {
  opacity: 0;
}
.carousel-fade .carousel-inner .active,
.carousel-fade .carousel-inner .next.left,
.carousel-fade .carousel-inner .prev.right {
  opacity: 1;
}
.carousel-fade .carousel-inner .next,
.carousel-fade .carousel-inner .prev,
.carousel-fade .carousel-inner .active.left,
.carousel-fade .carousel-inner .active.right {
  left: 0;
  transform: translate3d(0, 0, 0);
}
.carousel-fade .carousel-control {
  z-index: 2;
}
.main > article {
  margin: 5% 5%;
  text-align: justify;
}
.main > article ol,
.main > article ul {
  margin-right: 30px;
}
.main > article h1,
.main > article h2,
.main > article h3 {
  text-align: right;
}
#member_article {
  margin: 2.5% 5% 5% 5%;
}
.sidebar > aside {
  padding: 5%;
}
@media (max-width: 767px) {
  .sidebar > aside {
    padding: 5%;
  }
}
#branding img {
  margin: 20px 25px 5px 0;
  max-width: 450px;
}
@media (max-width: 767px) {
  #branding img {
    margin: 20px 10px 5px 0;
    width: 90%;
  }
}
.alert-dismissible {
  display: inline-block;
  position: fixed;
  left: 15px;
  top: 30px;
  z-index: 100;
}
.modal {
  direction: ltr;
}
.modal-content {
  direction: rtl;
}
@media screen and (min-width: 768px) {
  #YouTubeModal {
    margin-right: 100px;
  }
}
.accordion-heading {
  background: #415B76;
  border-radius: 5px;
}
.accordion-heading a {
  color: white;
}
.about article {
  margin: 0;
  padding: 0;
}
.about h2 {
  padding: 0;
  border-bottom: 1px dotted #415B76;
}
.about h3 {
  color: #E74C3C;
  padding: 0;
  margin: 5px;
  font-size: 1.5em;
}
.nav-tabs li a {
  color: #415B76;
}
.nav-tabs li.active a {
  background: #F5F5DC;
}
.nav-tabs li a:hover {
  background: #E74C3C;
  color: white;
}
kbd {
  padding: 0 2px;
  font-size: 80%;
  color: white;
  background-color: #333;
  border-radius: 10px;
  -webkit-box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.25);
  box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.25);
  font-family: "bbcnassim", "Helvetica Neue", Helvetica, Tahoma, Verdana, Arial, sans-serif;
}
.nav-tabs li.active a {
  color: #E74C3C;
  font-weight: bold;
}
.tab-content h3,
.tab-content h4 {
  margin: 0;
  padding: 0;
}
.tab-content h3 {
  color: #E74C3C;
  line-height: 1em;
}
.tab-content p {
  padding-top: 10px;
  text-align: justify;
}
ul.breadcrumb a:hover {
  color: #E74C3C;
}
.container-footer {
  background: #415B76;
}
footer ul.breadcrumb {
  background: #415B76;
  margin: 0 auto;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
  color: white;
  font-size: 80%;
}
footer ul.breadcrumb a > i:hover {
  color: #F0C832;
  transition-duration: 1s;
}
footer ul.breadcrumb i {
  color: white;
}
footer ul.breadcrumb li {
  text-shadow: none;
}
footer ul.breadcrumb li a {
  color: white;
}
.navbar .nav > li > .dropdown-menu {
  top: 30px;
}
.media-left,
.media > .pull-left {
  padding-left: 0;
}
.media-right,
.media > .pull-right {
  padding-right: 0;
}
#notifyModal {
  direction: rtl;
}
#notifyModal .media,
#notifyModal .media .media {
  margin-top: 0;
}
#notifyModal .modal-dialog {
  padding: 0;
}
#notifyModal .modal-content {
  border-radius: 0;
  height: auto;
  min-height: 100%;
}
@media screen and (min-width: 768px) {
  #notifyModal .modal-dialog {
    left: 0;
    right: 0;
    width: 33%;
    margin-left: 0;
    padding-top: 0;
    padding-bottom: 0;
    height: 100%;
  }
}
.scrollspy .nav {
  background: #415B76;
  margin-top: 20px;
}
@media (max-width: 979px) {
  .scrollspy .nav {
    width: 15%;
    max-width: 120px;
  }
}
@media (min-width: 980px) {
  .scrollspy .nav (max-width: 1199) {
    width: 13%;
    min-width: 120px;
  }
}
@media (min-width: 980px) and (min-width: 1200px) {
  .scrollspy .nav {
    width: 13%;
    max-width: 180px;
  }
}
.scrollspy .nav li {
  padding: 0;
  margin: 0;
  border-bottom: 1px solid #2f4255;
  border-top: 1px solid #537497;
}
.scrollspy .nav li a {
  color: white;
  text-indent: -20px;
  padding-left: 30px;
}
.scrollspy .nav li a:hover {
  background: #F0C832;
  color: black;
  transition: background, color 0.5s ease;
}
.scrollspy .nav li.active {
  background: #E74C3C;
}
.scrollspy .nav li:first-child {
  border-top: none;
}
.scrollspy .nav li:last-child {
  border-bottom: none;
}
.pagination {
  border-left: 1px solid #CCC;
}
.label-as-badge {
  display: inline-block;
  min-width: 10px;
  border-radius: 1em;
  margin-top: 3px;
}
.breadcrumb > li:after {
  display: inline-block;
  content: "";
  padding: 0 5px;
  color: #CCC;
}
.jumbotron h1 {
  font-size: 50px;
  color: #F5F5DC;
}
.jumbotron {
  padding: 30px;
  margin-bottom: 30px;
  font-size: 24px;
  font-weight: 200;
  line-height: 2.14285714;
  border-radius: 0;
  color: inherit;
  background-color: #303030;
}
.jumbotron p {
  color: #F5F5DC;
  font-size: 80%;
}
.table-responsive {
  width: 100%;
  margin-bottom: 15px;
  overflow-y: hidden;
  overflow-x: scroll;
  -ms-overflow-style: -ms-autohiding-scrollbar;
  -webkit-overflow-scrolling: touch;
}
.embed-responsive {
  position: relative;
  display: block;
  height: 0;
  padding: 0;
  overflow: hidden;
}
.embed-responsive .embed-responsive-item,
.embed-responsive iframe,
.embed-responsive embed,
.embed-responsive object,
.embed-responsive video {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  border: 0;
}
.embed-responsive-16by9 {
  padding-bottom: 56.25%;
}
.embed-responsive-4by3 {
  padding-bottom: 75%;
}
.center {
  margin: auto;
  text-align: center !important;
  display: inherit;
}
.center * {
  text-align: center !important;
}
.author-photo {
  width: 100px;
  height: 100px;
  margin-bottom: 30px;
  box-shadow: 1px 1px 10px #737373;
}
.btn {
  margin: 1% 0;
}
.selected {
  font-weight: bold;
  color: #E74C3C !important;
}
.range-wrap {
  overflow: hidden;
}
.glyphicon-user:before {
  display: block;
}
form.addtoplaylist,
form.removefromplaylist {
  display: inline;
}
.input-group-addon:first-child {
  border-right: 1px solid #CCC;
}
.input-group input:last-child,
.input-group-addon:last-child,
.input-group-btn:first-child > .btn:not(:first-child),
.input-group-btn:last-child > .btn,
.input-group-btn:last-child > .dropdown-toggle {
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
  border-bottom-left-radius: 4px;
  border-top-left-radius: 4px;
}
.dl-horizontal dt {
  float: right;
}
.bgvideo {
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
}
.screenshot {
  margin: 0 10px 10px 0;
}
.fb_iframe_widget,
.fb_iframe_widget span,
.fb_iframe_widget span iframe[style] {
  min-width: 100% !important;
  width: 100% !important;
}
.share-buttons {
  opacity: .6;
  transition-duration: 1s;
}
.share-buttons:hover {
  opacity: 1;
  transition-duration: 1s;
}
.admin-jumbotron {
  background: url('../../images/others/admin.jpg') no-repeat;
  -webkit-background-size: 100% 100%;
  -moz-background-size: 100% 100%;
  -o-background-size: 100% 100%;
  background-size: 100% 100%;
  background-size: cover;
  text-shadow: black 0.3em 0.3em 0.3em;
}
.author-jumbotron {
  background: url('../../images/others/author.jpg') no-repeat;
  -webkit-background-size: 100% 100%;
  -moz-background-size: 100% 100%;
  -o-background-size: 100% 100%;
  background-size: 100% 100%;
  background-size: cover;
  text-shadow: black 0.3em 0.3em 0.3em;
}
.member-jumbotron {
  background: url('../../images/others/member.jpg') no-repeat;
  -webkit-background-size: 100% 100%;
  -moz-background-size: 100% 100%;
  -o-background-size: 100% 100%;
  background-size: 100% 100%;
  background-size: cover;
  text-shadow: black 0.3em 0.3em 0.3em;
}
#twitter-widget-1 {
  width: 80% !important;
  margin: 0 auto !important;
  text-align: center !important;
  display: block !important;
}
