//
// Buttons
// --------------------------------------------------

// Base styles
// --------------------------------------------------

// Core styles
.btn {
	display        : inline-block;
	padding        : @padding-base-vertical @padding-base-horizontal;
	margin-bottom  : 0; // For input.btn
	font-size      : @font-size-base;
	font-weight    : 500;
	line-height    : @line-height-base;
	text-align     : center;
	vertical-align : middle;
	cursor         : pointer;
	border         : 1px solid transparent;
	border-radius  : @border-radius-base;
	white-space    : nowrap;

	&:focus {
		.tab-focus();
	}

	&:hover,
	&:focus {
		color           : #FFF;
		text-decoration : none;
	}

	&:active,
	&.active {
		outline          : 0;
		background-image : none;
		.box-shadow(inset 0 3px 5px rgba(0, 0, 0, .125));
	}

	&.disabled,
	&[disabled],
	fieldset[disabled] & {
		cursor         : default;
		pointer-events : none; // Future-proof disabling of clicks
		.opacity(.65);
		.box-shadow(none);
	}

}

// Alternate buttons
// --------------------------------------------------

.btn-default {
	.btn-pseudo-states(@btn-default-color, @btn-default-bg, @btn-default-border);
}

.btn-primary {
	.btn-pseudo-states(@btn-primary-color, @btn-primary-bg, @btn-primary-border);
}

// Warning appears as orange
.btn-warning {
	.btn-pseudo-states(@btn-warning-color, @btn-warning-bg, @btn-warning-border);
}

// Danger and error appear as red
.btn-danger {
	.btn-pseudo-states(@btn-danger-color, @btn-danger-bg, @btn-danger-border);
}

// Success appears as green
.btn-success {
	.btn-pseudo-states(@btn-success-color, @btn-success-bg, @btn-success-border);
}

// Info appears as blue-green
.btn-info {
	.btn-pseudo-states(@btn-info-color, @btn-info-bg, @btn-info-border);
}

// Link buttons
// -------------------------

// Make a button look and behave like a link
.btn-link,
.btn-link:active,
.btn-link[disabled],
fieldset[disabled] .btn-link {
	background-color : transparent;
	background-image : none;
	.box-shadow(none);
}

.btn-link,
.btn-link:hover,
.btn-link:focus,
.btn-link:active {
	border-color : transparent;
}

.btn-link {
	color         : @link-color;
	font-weight   : normal;
	cursor        : pointer;
	border-radius : 0;
}

.btn-link:hover,
.btn-link:focus {
	color            : @link-hover-color;
	text-decoration  : underline;
	background-color : transparent;
}

.btn-link {
	&[disabled],
	fieldset[disabled] & {
		&:hover,
		&:focus {
			color           : @gray-dark;
			text-decoration : none;
		}
	}
}

// Button Sizes
// --------------------------------------------------

.btn-large {
	padding       : @padding-large-vertical @padding-large-horizontal;
	font-size     : @font-size-large;
	border-radius : @border-radius-large;
}

.btn-small {
	padding       : @padding-small-vertical @padding-small-horizontal;
	font-size     : @font-size-small;
	line-height   : 1.5; // ensure proper height of button next to small input
	border-radius : @border-radius-small;
}

// Block button
// --------------------------------------------------

.btn-block {
	display       : block;
	width         : 100%;
	padding-left  : 0;
	padding-right : 0;
}

// Vertically space out multiple block buttons
.btn-block + .btn-block {
	margin-top : 5px;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
	&.btn-block {
		width : 100%;
	}
}
